/* eslint-disable no-useless-concat */


import React, {useState}        from 'react'
import {
    useSelector,
    useDispatch,
}                               from 'react-redux'

import {
    _Date,
    TIME_SLOTS,
}                               from 'JS/services.mjs'
import {useIntersect}           from 'JS/customHooks.js'

import {
    store,
    SELECTORS,
}                               from 'JS/store.js'



import {useBus, evDispatch}             from 'JS/useBus'


//CondFofmat sub functions
function getTrClassName(ri) {
    let groupName = (ri <= 18) ? 'groupN' : 'groupD'

    if (ri % 2)               { groupName += ' odd' }
    if (ri == 37 || ri == 41) { groupName += ' trPrimeTime' }
    return groupName
}
function getTdClassName({ minTime, ri, freeTimeVal, HDay, isPast}) {
    let tdClassName = ''
    if (freeTimeVal > 28) {
        tdClassName = 'fullTime'
    } else if (freeTimeVal < 2) {
        tdClassName = 'noTime'
    } else if (freeTimeVal < minTime) {
        tdClassName = 'lessTime'
    }

    if (HDay && (ri >= 21 && ri <= 41)) {
        tdClassName += ' ' + 'tdHDay'
    }

    if (isPast) {
        tdClassName += ' ' + 'isPast'
    }
    return tdClassName
}
//End CondFofmat sub functions




function doTdArr({ minTime, bokingDataObj }) {
    const NOW_DATE_N    = _Date.dateMsToDateN(Date.now())
    let days            = Object.keys(bokingDataObj)
    let tdArr           = Array.from(TIME_SLOTS, () => [])
        tdArr.unshift([])

    days.forEach( (dayN, iDay) => {
        let dayObj  = _Date.dayNumToCustomDateObj(+dayN)
        let HDayStr = dayObj.HDay ? 'HDay' : ''
        let dateStr = `${dayObj.dayN}/${dayObj.monthN}`
        let isPast  = dayN < NOW_DATE_N
        let ci      = iDay+1
        tdArr[0][ci] = (
            <td key={ci}
                className={ `td-header ${HDayStr} ${isPast ? 'isPast' : ''}`}
            >
                {dateStr}
            </td>)

        adjRows({ci, dayBokingData: bokingDataObj[dayN], tdArr, HDay : HDayStr, isPast, minTime})
    })
    return tdArr
}

function adjRows({ci, dayBokingData, tdArr, HDay, isPast, minTime}) {
    let curDayTimeSlots    = Object.keys(dayBokingData)
    curDayTimeSlots.forEach( (slot, iSlot) => {
        let ri             = iSlot+1
        let tdID           = 'r' +ri +'c' +ci
        let freeTimeVal    = 30 - dayBokingData[slot][1]
        let tdClass        = getTdClassName({freeTimeVal, minTime, ri, HDay, isPast})
        tdArr[ri][ci] = (
            <td key       = {tdID}
                className = {tdClass}
            >
                {freeTimeVal}
            </td>
            )
    })
}// end for rows




const Tr0 = ({children}) => {

    useBus(
        '@@ui/NEXT_NAV_BTN_CLICK',
        () => console.dir(children.length),
        [children]
    )

    return (
        <tr className="tableHeader">
            <th id="r0c0" className="th0">Slot</th>
            {children}
        </tr>
    )
}

function TrSlot( {ri, thText, children } ) {
    let groupClass = getTrClassName(ri)
    return (
        <tr className={groupClass}>
            <th className={groupClass} id={`r${ri}c0`}>{thText}</th>
            {children}
        </tr>
    )
}



function FreetimeTable() {
    const timeSlots     = TIME_SLOTS
    const bokingDataObj = useSelector( SELECTORS.bookingsObj )
    const minTime       = useSelector( SELECTORS.minTime )

    // let days            = Object.keys(bokingDataObj)
    let tdArr           = doTdArr({minTime, bokingDataObj})

    //Init first/header row
    let trArr        = [<Tr0 key="tr0">{tdArr[0]}</Tr0>]

    //Init data rows
    timeSlots.forEach( (slot, iSlot) => {
        let timeHeader    = timeSlots[iSlot][2]
        trArr[iSlot+1]    = <TrSlot
                                ri     = { iSlot + 1 }
                                thText = { timeHeader }
                                key    = { timeHeader }
                            >
                                {tdArr[iSlot + 1]}
                            </TrSlot>
    })

    return(
        <table id="mainTable" className="freetimeTable">
            <tbody id="freeTimeTbody">
                {trArr}
            </tbody>
        </table>
    )
}

export default FreetimeTable