/* eslint eqeqeq: "off", curly: "error" */
import { useState }                  from "react"

import { useSelector,
         useDispatch,
         shallowEqual, }             from 'react-redux'
import { setBasepath,
         useRoutes,
         navigate,
         useRedirect }               from 'hookrouter'

import { useBus,
         evDispatch }         from 'JS/useBus'

import { APP_CONSTANTS,
         store,
         toggleSidebarMD,
         sidebar_curObjUPD,
         toggleFlyerEditorMD,
         tableDateUPD,
         SELECTORS }          from 'JS/store.js'

import { getFlyersData, _Date}     from 'index'


import BottomBar         from 'components/BottomBar.jsx'
import ButtonBar         from 'components/ButtonBar.jsx'
import BookingsTable     from 'components/BookingsTable.jsx'
import FreetimeTable     from 'components/FreetimeTable.jsx'

import LoginForm         from 'Features/Login'
import FlyersEditor      from 'Features/FlyersEditor'
import { BookingEditor,
         GET_DEFAULT_BOOKING_VALS,} from 'Features/BookingEditor'

setBasepath(APP_CONSTANTS.BASE_PATH)

const routes = {
    '*bookings': () => ({mode: 'bookings', Content: BookingsTable}),
    '*freetime': () => ({mode: 'freetime', Content: FreetimeTable}),
    '*login':    () => ({mode: 'login', Content: LoginForm}),

    'default':   () => ({mode: 'bookings', Content: BookingsTable}),
}

// const USER = SELECTORS.userObj( store.getState() )

function App() {
    useRedirect('/*', '/bookings')

    const USER              = useSelector(SELECTORS.userObj)
    if (!USER.role) { navigate('/login') }

    const {mode, Content}   = useRoutes(routes) || routes.default()

    const sidebar_isToggled     = useSelector(SELECTORS.sidebar_isToggled)
    const flyersData_showEditor = useSelector(SELECTORS.flyersData_showEditor)

    const [sidebarKey, setSidebarKey] = useState(0)

    useBus(
        '@@ui/ADDREC_BTN',
        (e) => {
            let TR0           = findTR0node(e.payload)
            let [dayN, slot]  = TR0.id.split('_')
            let [hours, mins] = slot.split(':')
            let  time         = dayN * 86400000 + (+mins +hours*60)*60000

            let activeSidebarObj = SELECTORS.sidebar_isToggled(store.getState())
                                    ?   SELECTORS.sidebar_curObj(store.getState())
                                    :   null
            if (activeSidebarObj?.time === time) { return }

            let bookingObj    = activeSidebarObj
                ?   {
                        ...activeSidebarObj,
                        submitOptions: {toNew: true, toMove: true},
                        time
                    }
                :   {
                        time,
                        flyerId: 'TOSET',
                        submitOptions: {toNew: true},
                    }

            store.dispatch(sidebar_curObjUPD(bookingObj))

            setSidebarKey(key => key + 1)
            store.dispatch(toggleSidebarMD(true))
        }
    )
    useBus(
        '@@ui/EDITREC_BTN',
        (e) => {
            let bookingId  = Number(e.payload.innerText)

            let rawBookingsData = store.getState().mainState.bookingsData.raw
            let rawDataMap      = APP_CONSTANTS.bookingsAPIDataMap
            let rawBookingObj   = getRawBookingRec(rawBookingsData, rawDataMap, bookingId)

            store.dispatch(sidebar_curObjUPD(rawBookingObj))

            setSidebarKey(key => key + 1)
            store.dispatch(toggleSidebarMD(true))
        }
    )

    useBus(
        '@@ui/HOME_BTN_click', (e) => {
            let reqDate = _Date.dateMsToDateN( Date.now() )
            store.dispatch(tableDateUPD(reqDate))
        }
    )

    useBus(
        '@@ui/TEST_BTN3',
        (e) => {
            //debugger
            //console.log('APP.jsx see EDITREC_BTN click ', e)
            // store.dispatch(toggleSidebarMD())
            store.dispatch(tableDateUPD(19100))
            // getFlyersData({})
            // navigate('/login')
        }
    )


    return (
        <div className="App">
            <ButtonBar mode = { mode }/>
            <div id="contentWrapper">
                <div id = "inner" className="inner">
                    <Content />
                </div>
                {sidebar_isToggled     && <BookingEditor key = {sidebarKey}/>}
                {flyersData_showEditor && <FlyersEditor  key = {1}/>}
            </div>
            <BottomBar />
        </div>
    )
}

export default App




function findTR0node(childNode) {
    let node = childNode
    while (node.nodeName !== 'TR') {
        node = node.parentNode
    }
    while (!node.id) {
        node = node.previousElementSibling
    }
    return node
}

function getRawBookingRec(rawBookingsData, dataMap, bookingId) {
    let bookingRec = rawBookingsData.find(element => element[0] === bookingId)
    let obj        = {}
    for (const field in dataMap) {
        obj[field.replace('CI','')] = bookingRec[ dataMap[field] ]
    }
    return obj
}











